import './App.css';
import Home from "./pages/Home";
import Walking from './pages/Walking';
import Rain from "./pages/Rain";
import FunkySnake from './pages/FunkySnake';
import { Route, Switch } from "react-router-dom";

function App() {
  return (
    <>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/walking_p5" component={Walking} />
        <Route path="/rain_p5" component={Rain} />
        <Route path="/funky_snake" component={FunkySnake} />
      </Switch>
    </>
  );
}

export default App;
